  height: auto;
  transform: scale(0);
  transition: all 200ms 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
body .mask.dialog-layer .dialog .title {
  width: 4.7rem;
  height: 0.7rem;
  font-size: 0.22rem;
  font-weight: 600;
  color: #fbab22;
  line-height: 0.7rem;
  text-align: center;
  position: relative;
  border-bottom: 0.01rem solid #d2d2d2;
}
body .mask.dialog-layer .dialog .title .title-text {
  width: 1.2rem;
  height: 100%;
  line-height: 0.8rem;
  border-bottom: 0.02rem solid #fbab22;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -0.6rem;
  margin-top: -0.35rem;
  font-weight: 600;
  font-size: 0.22rem;
}
body .mask.dialog-layer .dialog .title .close-btn {
  width: 0.2rem;
  height: 0.2rem;
  background: url(./0f1cd2c842b50b020eb5f8916e128be1.png) no-repeat center center;
  background-size: 0.16rem auto;
  position: absolute;
  right: 0.2rem;
  top: 0.2rem;
}
body .mask.dialog-layer .dialog .title.clear {
  background: rgba(0, 0, 0, 0);
}
body .mask.dialog-layer .dialog .content {
  display: block;
  margin: 0;
