  width: 8.2rem;
  margin: 0 auto 0.3rem;
  background: #fff;
}
body .cal-bd {
  border-bottom: 0.01rem solid #cccccc;
}
body .highlight {
  color: #ff2a2a;
}
body .calendar-head {
  height: 0.46rem;
  display: flex;
  align-items: center;
}
body .calendar-head span {
  flex: 1;
  text-align: center;
}
body .calendar-control {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 0.5rem;
  color: #000;
}
body .calendar-control .control-l,
body .calendar-control .control-r {
  width: 0.6rem;
  height: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .calendar-control .control-l img,
body .calendar-control .control-r img {
  width: 0.07rem;
}
body .calendar-control .control-r img {
  transform: scaleX(-1);
}
body .calendar-control .control-c {
  text-align: center;
  margin: 0 0.3rem;
}
body .calendar-row {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 0.46rem;
}
body .calendar-col {
  width: 0.33rem;
  height: 0.33rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: relative;
}
body .calendar-col.disabled {
  color: #fff;
  background: #cacaca;
}
body .calendar-col.selected {
  color: #fff;
  background: #ff2a2a;
}
body .calendar-col.highlighted::after {
  content: "";
