  width: 0.06rem;
  height: 0.06rem;
  border-radius: 50%;
  background: #ff2a2a;
  bottom: 0;
  left: 50%;
  margin-left: -0.03rem;
}
body .toast-box {
  width: 100%;
  height: 0.5rem;
  position: fixed;
  bottom: 1rem;
}
body .toast-box .toast {
