}
body .toast-box .toast p {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 0.2rem;
  color: #fff;
  font-size: 0.14rem;
  padding: 0.14rem 0.26rem;
  box-sizing: border-box;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
